.navigation {
    width: 250px;
    max-width: 450px;
    @media screen and (max-width: 840px) {
        width: 150px;
    }
    @media screen and (max-width: 610px) {
        width: 120px;
    }
    @media  screen and (max-width: 540px) {
        width: 90px;
    }
    ul {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        transition: 1s ease-in-out;
        @media screen and (max-width: 300px) {
            justify-content: center;
        }
        a {
            position: relative;
            margin: 15px;
            transition: .5s ease-in-out;
            font-size: 1.3rem;
            &:hover:not(.nav-active) {
                transform: scale(1.1);
            }
            li {
                list-style: none;
                @media  screen and (max-width: 840px) {
                    display: none;
                }
            }
            span {
                position: absolute;
                top: 5%;
                left: 102%;
                @media screen and (max-width: 300px) {
                    font-size: 1.2rem;
                }
            }
        }
    }
    .nav-active {
        color: rgb(192, 0, 0);
    }
    .nav {
        color: rgb(14, 14, 14);
    }
}