.movies {
    min-height: 100vh;
    background: #8e0e00;
    background: -webkit-linear-gradient(to bottom, #8e0e00, #1f1c18);
    background: linear-gradient(to bottom, #8e0e00, #1f1c18);
    .main-container {
        .movies-container {
            .sort-container {
                .sort-content {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 20%;
                    margin: 0 auto;
                    @media screen and (max-width: 420px) {
                        width: 100%;
                    }
                    select {
                        margin-left: 10px;
                        background: none;
                        border: none;
                        &:focus-visible {
                            outline: none;
                        }
                    }
                }
            }
            .movie-content {
                width: 80%;
                display: flex;
                flex-wrap: wrap;
                padding: 10px 0 20px 0;
                margin: 0 auto;
                .card {
                    flex: 1 0 20%;
                    min-width: 230px;
                    max-width: 370px;
                    max-height: 400px;
                }
            }
        }
    }
}