.error-container {
    display: grid;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 100vh;
    background: #200122;
    background: -webkit-linear-gradient(to right, #5f0000, #200122);
    background: linear-gradient(to right, #5f0000, #200122);
    .logo {
        font-size: 2.5rem;
        transition: 0.6s ease-in-out;
        &:hover {
            transform: scale(1.2);
        }
    }
    .credits {
        position: absolute;
        top: 95%;
        left: 50%;
        transform: translateX(-50%);
    }
}