@font-face {
    font-family: "font1";
    src: url("../fonts/FjallaOne-Regular.ttf");
};
@font-face {
    font-family: "font2";
    src: url("../fonts/Jost-Light.ttf");
};

$font1: "font1", serif;
$font2: "font2", sans-serif;

body {
    overflow-x: hidden;
    min-height: 100vh;
    user-select: none;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a, a:visited {
    color: rgb(14, 14, 14);
    text-decoration: none;
    font-family: $font2;
}