.card {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgba(0, 0, 0, 0.37);
    border-radius: 15px;
    margin: 10px;
    padding: 10px;
    @media  screen and (max-width: 420px) {
        margin: 10px 5px 10px 0;
    }
    .img-container {
        position: relative;
        overflow: hidden;
        &:hover .infos, .infos:hover {
            visibility: visible;
            transform: translate(-50%, -50%);
        }
        .infos {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, 150%);
            visibility: visible;
            transition: 0.5s ease-in-out;
            h2 {
                color: rgb(192, 0, 0);
                text-align: center;
                margin-bottom: 15px;
                font-size: 1.2rem;
            }
            p {
                position: relative;
                color: rgba(255, 255, 255, 0.493);
                text-align: center;
                margin-bottom: 15px;
                #star {
                    position: absolute;
                    top: 15%;
                    left: 58%;
                    color: rgba(255, 255, 0, 0.658);
                    font-size: 0.8rem;
                    margin-left: 5px;
                }
            }
            .genre-container {
                display: flex;
                min-width: 200px;
                max-width: 360px;
                li {
                    background: rgba(255, 255, 255, 0.493);
                    border-radius: 10px;
                    list-style: none;
                    color: rgb(12, 12, 12);
                    font-family: $font2;
                    padding: 5px;
                    margin: 5px;
                    @media  screen and (max-width: 420px) {
                        font-size: 0.8rem;
                    }
                }
            }
        }
        img {
            width: 100%;
            height: 100%;
            border-radius: 5px;
            margin-bottom: 5px;
            &:hover {
                filter: opacity(15%) contrast(150%);
            }
        }
    }
    .like {
        background: rgba(0, 0, 0, 0.767);
        color: #5f0000;
        border-radius: 25px;
        width: 150px;
        padding: 5px 10px;
        margin: 10px 0;
        text-align: center;
        font-size: 0.8rem;
        font-weight: 600;
        transition: 0.3s ease-in-out;
        &:hover {
            color: rgb(192, 0, 0);
        }
    }
}