.searchbar-container {
    display: flex;
    flex-direction: column;
    .form-content {
        display: grid;
        margin: 0 auto;
        input[type="text"] {
            padding: 5px;
            margin: 0 0 15px 0;
            background: none;
            border: none;
            border-bottom: 2px solid black;
            border-radius: 5px;
            color: rgba(255, 255, 255, 0.493);
            font-size: 1.2rem;
            text-align: center;
            &::placeholder {
                color: black;
            }
            &:focus-visible {
                outline: none;
            }
            &::spelling-error {
                text-decoration: none;
            }
            @media  screen and (max-width: 420px) {
                font-size: 1rem;
            }
        }
        input[type="submit"] {
            margin: 0 auto 25px;
            border-radius: 25px;
            padding: 5px;
            min-width: 125px;
            max-width: 150px;
            cursor: pointer;
            transition: 0.6s ease-in-out;
            background: rgb(2, 2, 2);
            color: rgb(192, 0, 0);
            &:hover {
                -webkit-box-shadow: inset -1px -1px 6px 2px rgba(39, 39, 39, 0.56); 
                box-shadow: inset -1px -1px 6px 2px rgba(39, 39, 39, 0.56);
            }
        }
    }
}