.header-container {
    display: grid;
    justify-items: center;
    grid-template-columns: 30% 40% 30%;
    padding: 25px;
    @media screen and (max-width: 300px) {
        grid-template-columns: 15% 70% 15%;
    }
    @media screen and (max-width: 420px) {
        padding: 25px 10px;
    }
    .logo {
        margin: 0 auto;
    }
    .other-nav {
        width: 250px;
        max-width: 450px;
        @media screen and (max-width: 840px) {
            width: 150px;
        }
        @media screen and (max-width: 610px) {
            width: 120px;
        }
        @media  screen and (max-width: 540px) {
            width: 90px;
        }
        ul {
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            @media screen and (max-width: 300px) {
                justify-content: start;
            }
            a {
                position: relative;
                font-size: 1.3rem;
                margin: 15px;
                transition: 0.5s ease-in-out;
                &:hover:not(.nav-active) {
                    transform: scale(1.1);
                }
                li {
                    list-style: none;
                    @media  screen and (max-width: 840px) {
                        display: none;
                    }
                }
                span {
                    position: absolute;
                    top: 5%;
                    left: 102%;
                    @media screen and (max-width: 300px) {
                        font-size: 1.2rem;
                    }
                }
            }
        }
        .nav-active {
            color: rgb(192, 0, 0);
        }
        .nav {
            color: rgb(14, 14, 14);
        }
    }
}