.logo {
    color: rgb(192, 0, 0);
    text-shadow: 2px -1px 2px #000000;
    font-size: 1.8rem;
    font-family: $font1;
    h1 {
        @media screen and (max-width: 300px) {
            font-size: 1.6rem;
        }
        @media  screen and (max-width: 550px) {
            font-size: 2.3rem;
        }
    }
}