.favorites {
    min-height: 100vh;
    background: #8e0e00;
    background: -webkit-linear-gradient(to bottom, #8e0e00, #1f1c18);
    background: linear-gradient(to bottom, #8e0e00, #1f1c18);
    .movies-container  {
        .movie-content {
            display: flex;
            flex-wrap: wrap;
            padding: 30px 80px 50px 80px;
            p {
                position: relative;
                margin: 40px auto 0;
                font-size: 1.3rem;
                color: rgba(202, 202, 202, 0.808);
                b {
                    color: rgb(192, 0, 0);
                }
                span {
                    position: absolute;
                    left: 105%;
                    color: rgba(255, 115, 0, 0.63);
                }
            }
            h2 {
                color: rgba(202, 202, 202, 0.808);
                margin: 80px auto 0;
            }
        }
        .card {
            flex: 1 0 20%;
            min-width: 200px;
            max-width: 370px;
            max-height: 400px;
        }
    }
}