.home {
    min-height: 100vh;
    background: #8e0e00;
    background: -webkit-linear-gradient(to bottom, #8e0e00, #1f1c18);
    background: linear-gradient(to bottom, #8e0e00, #1f1c18);
    .trending-content {
        h2 {
            font-family: $font1;
            text-align: center;
            color: rgb(192, 0, 0);
            @media  screen and (max-width: 420px) {
                letter-spacing: 2px;
            }
        }
    }
    .movies-container  {
        margin: 0 auto;
        padding-bottom: 50px;
        width: 90%;
        .sort-container {
            display: flex;
            width: 100%;
            margin: 0 0 0 90px;
            padding: 10px;
            .sort-content {
                align-self: center;
                label {
                    text-align: left;
                    margin: 0 5px 0 0;
                }
                select {
                    background: none;
                    border: 1px solid black;
                    margin: 0 auto;
                    cursor: pointer;
                    &:focus-visible {
                        outline: none;
                    }
                }
            }
        }
        h2 {
            text-align: center;
            margin: 40px 0 20px 0;
            @media screen and (max-width: 420px) {
                font-size: 1.2rem;
            }
        }
        .movie-container, .serie-container {
            display: flex;
            align-items: center;
            width: 85%;
            margin: 0 auto;
            @media screen and (max-width: 420px) {
                width: 235px;
                margin: 0 auto;
            }
            .movie-content {
                position: relative;
                display: flex;
                align-items: center;
                overflow-y: hidden;
                padding: 20px 0 10px 0px;
                p {
                    position: relative;
                    margin: 40px auto 0;
                    font-size: 1.3rem;
                    color: rgba(202, 202, 202, 0.808);
                    b {
                        color: rgb(192, 0, 0);
                    }
                    span {
                        position: absolute;
                        left: 105%;
                        color: rgba(255, 115, 0, 0.63);
                    }
                }
            }
        }
        .card {
            min-width: 230px;
            max-width: 370px;
            max-height: 400px;
        }
    }
    ::-webkit-scrollbar {
        background-color: transparent;
    }
    ::-webkit-scrollbar-thumb {
        background-color: #131313;
        border-radius: 15px;
    }
    ::-webkit-scrollbar-track {
        background-color:  #8e0e0063;
        border-radius: 15px;
    }
}